import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import App from './App';
import LegalMentions from './LegalMentions';

const Routeur = () => {
	return(
	<Router>
        <Routes>
          <Route path="/webpage/legalmentions" element={<LegalMentions />} />
          <Route path="/webpage/" element={<App />} />
        </Routes>
    </Router>
	)
	
}

export default Routeur