import {useEffect, useRef} from 'react'
import {Canvas } from '@react-three/fiber'
import {PerspectiveCamera} from '@react-three/drei'
import Cuisine from './Cuisine.js'
import ChefBody from './Chefbody.js'
// import ChefInstancedBody from './Chefinstancedbody.js'
import ChefSecondBody from './Chefsecondbody.js'
import ChefLogo from './ChefLogo.js'
import Fourchette from './Fourchette.js'
import Couteau from './Couteau.js'
import Cuillere from './Cuillere.js'
import TasseCafeBlanc from './TasseCafeBlanc.js'
import TasseCafeSombre from './TasseCafeSombre.js'
import './App.css';

function App() {
	const knifeSpeed = 0.5;
	const forkSpeed = 0.2;
	const spoonSpeed = 0.3;
	const canvasRef = useRef()
	const canvasChefRef = useRef()
	const mainRef = useRef()
	const chefBodyContainerRef = useRef()
	const canvasContainerRef = useRef()
	const introOverlayRef = useRef()
	const spoonCanvas = useRef()
	const knifeCanvas = useRef()
	const forkCanvas = useRef()
	const forkContainerRef = useRef()
	const spoonContainerRef = useRef()
	const knifeContainerRef = useRef()
	const tasseBlancRef = useRef()
	const tasseSombreRef = useRef()
	useEffect(()=>{
		window.addEventListener('scroll',(e)=>{
			// console.log(window.pageYOffset)
			// canvasRef.current.style.transform="rotate(1 0 0 "+window.pageYOffset+"deg)"
			// mainRef.current.style.perspective="200px"
			// canvasRef.current.parent.style.perspective="200px"
			// canvasRef.current.parent.style.perspective="200px"
			if(canvasContainerRef){
				canvasContainerRef.current.style.perspective="200px"
				canvasContainerRef.current.style.scale=1-window.pageYOffset*0.01
				if(document)
				{
					if((window.pageYOffset/document.body.scrollHeight)*100>10)
					{
						knifeContainerRef.current.style.visibility="visible"
						forkContainerRef.current.style.visibility="visible"
						spoonContainerRef.current.style.visibility="visible"
						
						knifeContainerRef.current.style.top=(-150+(window.pageYOffset/document.body.scrollHeight)*900+((window.pageYOffset/document.body.scrollHeight)*200)*knifeSpeed)+'%';
						forkContainerRef.current.style.top=(-150+(window.pageYOffset/document.body.scrollHeight)*900+((window.pageYOffset/document.body.scrollHeight)*200)*forkSpeed)+'%';
						spoonContainerRef.current.style.top=(-150+(window.pageYOffset/document.body.scrollHeight)*900+((window.pageYOffset/document.body.scrollHeight)*200)*spoonSpeed)+'%';
					}
					else{
						knifeContainerRef.current.style.visibility="hidden"
						forkContainerRef.current.style.visibility="hidden"
						spoonContainerRef.current.style.visibility="hidden"
					}
					if(1-window.pageYOffset*0.01<0.5) {
						chefBodyContainerRef.current.style.visibility="visible"
						chefBodyContainerRef.current.style.marginLeft="0"
						introOverlayRef.current.style.visibility="hidden"
						chefBodyContainerRef.current.style.opacity=0.5
						// let currentKnifePos = 	parseFloat(knifeContainerRef.current.style.top.split('%')[0])+!parseFloat(knifeContainerRef.current.style.top.split('%')[0])||-50;
						// let currentForkPos = parseFloat(forkContainerRef.current.style.top.split('%')[0])+!parseFloat(forkContainerRef.current.style.top.split('%')[0])||-50;
						// let currentSpoonPos = parseFloat(spoonContainerRef.current.style.top.split('%')[0])+!parseFloat(spoonContainerRef.current.style.top.split('%')[0])||-50;
						// console.log(parseInt(knifeContainerRef.current.style.top.split('%')[0]))
					}
					else {
						chefBodyContainerRef.current.style.visibility="hidden"
						introOverlayRef.current.style.visibility="visible"
					}
					if(1-window.pageYOffset*0.01<0) {
						chefBodyContainerRef.current.style.opacity=1
						canvasContainerRef.current.style.visibility="hidden"
						canvasContainerRef.current.style.scale=0
					}
					else canvasContainerRef.current.style.visibility="visible"
					if((window.pageYOffset/document.body.scrollHeight)*100>25)
						chefBodyContainerRef.current.style.opacity=0.5
					if((window.pageYOffset/document.body.scrollHeight)*100>27.5)
						chefBodyContainerRef.current.style.marginLeft="70%"
					if((window.pageYOffset/document.body.scrollHeight)*100>30)
						chefBodyContainerRef.current.style.opacity=1
					// if((window.pageYOffset/document.body.scrollHeight)*100>45)
						// chefBodyContainerRef.current.style.opacity=0.5
					// if((window.pageYOffset/document.body.scrollHeight)*100>52)
						// chefBodyContainerRef.current.style.visibility="hidden"
				}
				canvasContainerRef.current.children[0].style.translate="50 0"
				// canvasRef.current.style.rotateX="45deg"
				canvasContainerRef.current.children[0].style.rotate="1 0 0 "+window.pageYOffset*0.3+"deg"
				// canvasContainerRef.current.children[0].style.marginLeft=window.pageYOffset*0.25+"%"
				// canvasRef.current.style.padding=window.pageYOffset*0.01+"%"
				// canvasRef.current.style.translate=window.pageYOffset*0.01+"% "+0+"%"
				// canvasRef.current.style.transform="scaleX(-.5)"
				// canvasRef.current.style.rotate="200px"
			}
		})
	},[])
	
	const goToMailbox = (e) => {
		e.preventDefault();
		// console.log(e.target.objet.value)
		window.location.href = "mailto:studioscroustillants@outlook.com?subject="
			+ encodeURIComponent(e.target.objet.value)
			+ "&body=" + encodeURIComponent(e.target.message.value);
	}
	
	// services
	// cours
	// projets
	// contact
		// <p><b>Concevoir et faire</b></p>
  return (
    <div className="App" ref={mainRef}>
	  <div className="IntroOverlay" ref={introOverlayRef}>
		<h1>Studios croustillants</h1>
		<p><b>Confection informatique</b></p>
	  </div>
	  <div id={"pageOne"} ref={canvasContainerRef} >
		  <Canvas
			colorManagement
			shadows
			ref={canvasRef}
		  >
			<directionalLight castShadow position={[1,1,1]} intensity={0.9} />
			<Cuisine />
			<PerspectiveCamera makeDefault far={100} near={0.1} fov={28} position={[1,7,15]} rotation={[-Math.PI/8, 0, 0]} />
			<ChefBody scale={2} rotation={[0,Math.PI,0]} position={[2,0,.5]}/>
			<ChefLogo scale={.35} rotation={[0,Math.PI/2,0]} position={[2,3.3,.5]}/>
		  </Canvas>
	  </div>
	  <div id={"pageTwo"}>
		<div id={"pageTwoElements"}>
			<div className={"service"} >
				<h3> #Maintenance </h3>
				<ul> 
					<li> Assemblage d'ordinateurs. </li>
					<li> Rparation d'ordinateurs. </li>
					<li> Remplacement de pices. </li>
				</ul>
				<img src={"postIt.png"} alt="Post it d'arrire plan pour la partie maintenance" />
			</div>
			<div className={"service"} >
				<h3> #Dveloppement logiciel </h3>
				<ul> 
					<li> Dveloppement de sites web. </li>
					<li> Rfrencement. </li>
					<li> Dveloppement de logiciels de bureautique. </li>
				</ul>
				<img src={"postItB.png"} alt="Post it d'arrire plan pour la liste des services de dveloppement" />
			</div>
			<div className={"service"} >
				<h3> #Formation </h3>
				<ul> 
					<li> Atelier dveloppement de jeux vido. </li>
					<li> Initiation aux outils de bureautique. </li>
					<li> Aide en mathmatiques. </li>
				</ul>
				<img src={"postIt.png"} alt="Post it d'arrire plan pour la liste des services de formation"/>
			</div>
		</div>
		<div id={"chefBodyCanvas"} ref={chefBodyContainerRef}>
			  <Canvas
				ref={canvasChefRef}
			  >
				<rectAreaLight intensity={0.9} rotation={[0,0,0]} position={[2,0,2.5]}/>
				<PerspectiveCamera makeDefault far={100} near={0.1} fov={28} position={[3,8,15]} rotation={[-Math.PI/8, 0, 0]} />
				<ChefSecondBody isInKitchen={false} scale={2} rotation={[0,0,0]} position={[2,0,.5]}/>
				<ChefLogo scale={.35} rotation={[0,-Math.PI/2,0]} position={[2,3.3,.5]}/>
			  </Canvas>
			</div>
		</div>
		<div id={"pageThree"}>
			<h2> Projets </h2>
			<div className="gooey">
				<a href="https://studioscroustillants.github.io/sitewebjoyeuse/" ><h6>Joyeuse</h6></a>
				<img src="SiteJoyeuse.png"  alt="L'un de nos prcdents projets, un jeu vido en 3D" />
			</div>
			<div id={"Fourchette"} ref={forkContainerRef}>
				<Canvas
						ref={forkCanvas}
					  >
					  <rectAreaLight intensity={0.9} rotation={[0,0,0]} position={[2,0,7.5]}/>
					  <PerspectiveCamera makeDefault far={100} near={0.1} fov={28} position={[3,11,20]} rotation={[-Math.PI/8, 0, 0]} />
					  <Fourchette />
				</Canvas>
			</div>
			<div id={"Couteau"} ref={knifeContainerRef}>
				<Canvas
						ref={knifeCanvas}
					  >
					  <rectAreaLight intensity={0.9} rotation={[0,0,0]} position={[2,0,2.5]}/>
					  <PerspectiveCamera makeDefault far={100} near={0.1} fov={28} position={[3,5,25]} rotation={[-Math.PI/8, 0, 0]} />
					  <Couteau rotation={[-Math.PI,0,0]} />
				</Canvas>
			</div>
			<div id={"Cuillere"} ref={spoonContainerRef}>
				<Canvas
						ref={spoonCanvas}
					  >
					  <rectAreaLight intensity={0.9} rotation={[0,0,0]} position={[2,0,2.5]}/>
					  <PerspectiveCamera makeDefault far={100} near={0.1} fov={28} position={[2,11,20]} rotation={[-Math.PI/8, 0, 0]} />
					  <Cuillere />
				</Canvas>
			</div>
		</div>
		<div id={"pageContact"}>
			<div id="contactCanvas">
				<Canvas
						ref={tasseSombreRef}
					  >
						  <rectAreaLight intensity={0.9} rotation={[0,0,0]} position={[2,0,2.5]}/>
						  <PerspectiveCamera makeDefault far={100} near={0.1} fov={28} position={[0,10,-2.3]} rotation={[-Math.PI/2, 0, 0]} />
						  <TasseCafeSombre />
					</Canvas>
				<Canvas
					ref={tasseBlancRef}
				  >
					  <rectAreaLight intensity={0.9} rotation={[0,0,0]} position={[2,0,2.5]}/>
					  <PerspectiveCamera makeDefault far={100} near={0.1} fov={28} position={[0,10,2.3]} rotation={[-Math.PI/2, 0, 0]} />
					  <TasseCafeBlanc />
				</Canvas>
			</div>
			<h3>Contactez nous</h3>
			<iframe title="Carte de l'endroit ou nous trouver" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2625.7252863192557!2d8.054570199999999!3d48.84437849999999!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x4796e4210c05e9b7%3A0x3500a28a0302cf2e!2s4%20Rue%20de%20l&#39;Or%2C%2067480%20Roppenheim!5e0!3m2!1ssv!2sfr!4v1706110917995!5m2!1ssv!2sfr" allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>
			<div className="verticalLine" ></div>
			<form onSubmit={goToMailbox}>
				<h4> Tel : 06 27 49 82 52 </h4>
				<input name="objet" type="text" placeholder="Objet" />
				<textarea name="message" placeholder="Message" />
				<input type="submit" value="Envoyer" />
			</form>
			<a className="MailBotCatch" href="https://www.randomlists.com/email-addresses">Bot catcher</a>
		</div>
	</div>
  );
}
		// <directionalLight position={[1,1,1]} intensity={0.9} />

export default App;
